
#ccHead{
   background-color: #0d3349;
   color:whitesmoke;
   height:50px;
   font-size:20px;
   line-height:50px;
   padding-left:25px;
}
#addFile
{
  height:50px;
  line-height:50px;
  float:right;
  margin-right:100px;
}

#ccContainer{
  left:350px;
  position: absolute;
  top: 50px;
  bottom: 0px;
  right:0px;
}
#ccEdit
{
  width: 100%;
  height:100%;
  border: none;
  outline: none;
  border-radius: 0;
  padding: 6px 12px;
  font-size: 16px;
  line-height: 1.42857143;
  color: #555;
  padding-top:5px;
}
.mytitle
{
  padding-left:10px;
  font-size: 1.8em;
  line-height: 1.8em;
  color: #0d3349;
}
.ccTitle
{
  width: auto;
  border: none;
  outline: none;
}

#myLeft
{
  left: 0px;
  width: 350px;
  position: absolute;
  top: 50px;
  bottom: 0px;
  background-color: #fcfcfc;
  overflow-y: auto;
}
ul
{
 list-style: none;
  padding:0;
}
.myitem
{
  height:110px;
  border-bottom: 1px solid #cccccc;
}
.mystart
{
  color:#F7AE4F;
  font-size:20px;
}
.content
{
  font-size: 14px;
  line-height: 1.57142857;
  color: #999;
  height:40px;
  overflow: hidden;
}
.myinfor
{
  font-size: 12px;
  color: #6b6b6b;
}
.myli:hover
{
  background-color: antiquewhite;
}
.ccTitle
{
  margin-bottom: 8px;
  font-size: 20px;
  line-height: 28px;
  color: #3d3d3d;
  font-weight: bold;
  padding-left:5px;
}
.myli.myactive
{
  color: #fff;
  background-color: #dae4e1;
}
.myicon
{
  font-size:16px;
  margin-right:10px;
}
.myicon:hover
{
  color:#428bca;
}


